@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?ljcr1o');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?ljcr1o#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?ljcr1o') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?ljcr1o') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?ljcr1o##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-calendar {
  &:before {
    content: $icon-calendar;
  }
}

.icon-arrow-right2 {
  &:before {
    content: $icon-arrow-right2;
  }
}

.icon-arrow-left2 {
